body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;
}

body {
  background-color: #7cc6fe;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.app {
  background-color: #252d4a;
  width: 550px;
  min-height: 300px;
  height: min-content;
  border-radius: 15px;
  padding: 40px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}

.answerbox {
  display: flex;
  font-size: 24px;
  align-items: center;
  flex-direction: column;
}

/* QUESTION/TIMER/LEFT SECTION */
.questionbox {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  width: 90%;
  font-size: 1.5rem;
  margin-bottom: 12px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answerbox {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  min-height: 60px;
  border-radius: 15px;
  display: flex;
  padding: 10px; 
  justify-content: center;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.correct {
  background-color: #2f922f;
}
.correct:hover{
background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}
.incorrect:hover {
  background-color: #ff3333;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

.scorebox{
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.restart{
  text-align: center;
  width: auto;
  margin: 10px;
  padding: 10px 30px;
}

@media  screen and  (max-width: 768px) {

  .app{
    width: 80%;
    height: 500px;
    margin: 0;
    flex-direction: column;
  }
  button{
    margin: 10px 0;
  } 
}